.shop_section{
    margin-top: 40px;
}
.header_content{
    display:flex;
    justify-content: center;
    margin: 60px 0px;
    height: 60px;
    align-items: center;
    width: 100%;
}

.header_content h2{
    margin: 0px 10px;
    font-size: xx-large;
}
.items_wrap{
    overflow: hidden;
    width: 90%;
    margin:auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-between;
}
.items{
    margin: 30px 5px;
    width:100%;
    display: flex;
}
.img_wrap{
    width: 40%;
    text-align: center;
}
.img_wrap img{
    width: 230px;
    height: auto;
}
#gym_ball{
    width:100%;
}
.specs{
    width:59%;
    margin-left: 4px;
}
.specs h3{
    margin-top: -1px;
}
.prices_specs{
    display: flex;
    justify-content: space-between;
}
.prices_specs b{
    margin-right: 30px;
}

.item_btn{
    margin-top: 10px;
    margin-left: 20px;
    width: 80px;
    border: none;
    height: 30px;
    color: white;
    background-color: #ffc107;
    font-size: 16px;
}
.item_btn:hover{
    box-shadow:  1px 1px 7px 0px rgba(0, 0, 0, 0.479);
}
@media screen and (max-width:1050px){
    .items_wrap{
        width: 95%;
    }
    .img_wrap img{
        width: 180px;
    }

}
@media screen and (max-width:950px){
    .items_wrap{
        width: 100%;
    }
    .img_wrap img{
        width: 200px;
    }
    #gym_ball{
        width: 200px;
    }
    .items_wrap{
        overflow: hidden;
        width: 80%;
        margin:auto;
        display: grid;
        grid-template-columns:1fr;
    }
    .img_wrap{
        width: 40%;
    }
    .prices_specs b{
        margin-right: 10px;
    }

}
@media screen and (max-width:800px){

    .items_wrap{
        overflow: hidden;
        width: 85%;
        margin:auto;
        display: grid;
        grid-template-columns:1fr;
    }
    .header_content h2{
        font-size: x-large;
    }

}
@media screen and (max-width:400px){
    .header_content h2{
        margin: 0px 1px;
        font-size: 18px;
    }
    .items_wrap{
        overflow: hidden;
        width: 99%;
        margin:auto;
        display: grid;
        grid-template-columns:1fr;
    }
    .items{
        margin:35px 0px;
    }
    .prices_specs{
        display: block;
        justify-content: space-between;
    }
    .prices_specs b{
        margin-left: 30px;
    }
    .img_wrap img{
        width:99%;
    }
    #gym_ball{
        width:99%;
    }
}
